"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axios from "axios";

// =============================
// Type Definition
// =============================
interface TerminalCounts {
  live: number;
  store: number;
  demo: number;
  debug: number;
  faulty: number;
}

// =============================
// Component
// =============================
export default function HomePage() {
  const [counts, setCounts] = useState<TerminalCounts | null>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // Fetch terminal counts from FastAPI
  useEffect(() => {
    axios
      .get("http://localhost:8000/terminals/counts")
      .then((res) => {
        setCounts(res.data);
        setLoading(false);
      })
      .catch((err) => {
        console.error("Error fetching terminal counts:", err);
        setLoading(false);
      });
  }, []);

  // Button Data with Routes
  const buttons = [
    { name: "Live Terminals", gradient: "from-green-300 via-green-400 to-green-500", value: counts?.live, route: "/live" },
    { name: "Store Terminals", gradient: "from-blue-300 via-blue-400 to-blue-500", value: counts?.store, route: "/store" },
    { name: "Demo Terminals", gradient: "from-yellow-300 via-yellow-400 to-yellow-500", value: counts?.demo, route: "/demo" },
    { name: "Debug Terminals", gradient: "from-purple-300 via-purple-400 to-purple-500", value: counts?.debug, route: "/debug" },
    { name: "Faulty Terminals", gradient: "from-red-300 via-red-400 to-red-500", value: counts?.faulty, route: "/faulty" },
    { name: "Task Update", gradient: "from-pink-300 via-pink-400 to-pink-500", value: counts?.faulty, route: "/tasks" },
  ];

  return (
    <div className="min-h-screen flex flex-col items-center bg-gradient-to-b from-gray-100 to-gray-200">
      
      {/* Header Section */}
      <header className="w-full bg-white shadow-md flex flex-col items-center py-4">
        <h1 className="text-2xl font-bold text-gray-600 tracking-wide text-center">
          BGI Inventory Management System
        </h1>
        <img
          src="/BGI-logo.jpg"
          alt="BGI Logo"
          className="h-36 object-contain mb-2"
        />
      </header>

      {/* Main Dashboard Section */}
      <main className="flex-1 flex flex-col justify-center items-center w-full p-8">
        {loading ? (
          <p className="text-gray-600 text-lg animate-pulse">
            Loading terminal data...
          </p>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-10 mt-6 max-w-6xl">
            {buttons.map((btn) => (
              <div
                key={btn.name}
                className={`
                  relative bg-gradient-to-br ${btn.gradient} 
                  text-white rounded-3xl shadow-lg p-12 flex flex-col items-center justify-center 
                  hover:scale-105 hover:shadow-2xl transform transition duration-300 cursor-pointer overflow-hidden
                `}
                onClick={() => router.push(btn.route || "/")}
              >
                {/* Glow Effect */}
                <span className="absolute inset-0 bg-white opacity-10 blur-3xl rounded-3xl animate-pulse-slow"></span>

                <h2 className="text-2xl font-semibold mb-3 text-center drop-shadow-sm">
                  {btn.name}
                </h2>
                <p className="text-6xl font-bold drop-shadow-md">
                  {btn.value !== undefined ? btn.value : "-"}
                </p>
              </div>
            ))}
          </div>
        )}
      </main>

      {/* Footer Section */}
      <footer className="w-full py-10 bg-white border-t flex flex-col items-center">
        {/* Partner/Profile Photos */}
        <div className="flex flex-wrap justify-center gap-10 mb-6">
          {["/photo1.jpeg", "/photo2.jpeg", "/photo3.jpeg", "/footer4.png", "/footer4.png"].map((src, idx) => (
            <img
              key={idx}
              src={src}
              alt={`Profile ${idx + 1}`}
              className="h-20 w-20 rounded-full object-cover border-4 border-gray-200 shadow-md transform transition-transform duration-300 ease-in-out hover:scale-125 hover:shadow-2xl"
            />
          ))}
        </div>

        <p className="text-gray-500 text-sm text-center">
          © {new Date().getFullYear()} BGI • All Rights Reserved
        </p>
      </footer>
    </div>
  );
}
